import { useNavigate } from "react-router-dom";
import React, { useEffect, useState } from "react";
import { LogOut } from "lucide-react";

import { Link, useLocation } from "react-router-dom";
function TopBar() {
  const navigate = useNavigate();
  const location = useLocation();
  const [userdataa, setuserdataa] = useState("");
  useEffect(() => {
    // Check if the username key exists in session storage
    const storedUsername = localStorage.getItem("adminLogin");
    const userLogin = JSON.parse(storedUsername);
    setuserdataa(userLogin);
    if (userLogin === null) {
      localStorage.removeItem("adminLogin");

      navigate("/admin/login");
    }
  }, []);
  return (
    <section className="d-block admin_top_bar w-100">
      <div className="container-xl">
        <div className="d-flex w-100 justify-content-end align-items-center gap-2">
          <div className="profile_admin">
            <img
              className="w-100 h-100 object-fit-cover"
              src={require("../../assets/admin/images/woman.jpg")}
              alt="logo"
            />
          </div>
          <div
            title="Logout"
            className="p-2 rounded  d-flex align-items-center gap-2  logout_btn"
            style={{ cursor: "pointer" }}
          >
            <Link
              to="/admin/logout"
              className="text-danger text-center d-flex align-items-center justify-content-center gap-2 text-decoration-none w-100"
            >
              <LogOut size={20} />
            </Link>
          </div>
        </div>
      </div>
    </section>
  );
}

export default TopBar;
